import React, { Component } from 'react';

import { Col, Row } from 'reactstrap';
//import { PeopleAlt, LocalShipping, MailOutline, SupervisorAccount, Link } from '@material-ui/icons';

import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import './dashboard.css'

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
     loading: false,
      dashBoardStats: {totalEmployeeUser: 0,
          totalCompanyUser: 0,
          totalCases: 0,
          totalNewCases: 0,
          totalInProgressCases: 0,
          totalClosedCases: 0 },
      userList: [],
      enquiryList:[],
     
    };
  }

  componentDidMount() { 
    this.setState( { loading: true}, () => {

      commonService.getAPIWithAccessToken('dashboard')
        .then( res => {
          if( undefined === res.data.data || !res.data.status ){
            this.setState( {loading: false });
            toast.error(res.data.message);
            return;
          }
          const dashData = res.data.data;

          this.setState({ loading:false, dashBoardStats:dashData})
        }
        )

      
        
      
      
    } )
  }
 

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  render() {

    return (
      <div className="animated fadeIn admin-dashboard">
        <div className="overview-info">

          <Row>
          <Col xs="12" sm="6" lg="4">
            <div className="card-bg-info card-bg-1">
              <div className="card-bg-icon">
                <i class="fa fa-building" aria-hidden="true"></i>
              </div>
              <div className="card-bg-content">
                <p>Total Organisation</p>

                <h2 className="text-value">0</h2>
              </div>
            </div>
          </Col>
          <Col xs="12" sm="6" lg="4">
            <div className="card-bg-info card-bg-2">
              <div className="card-bg-icon">
                <i class="fa fa-user-plus" aria-hidden="true"></i>
              </div>
              <div className="card-bg-content">
                <p>Active Subscribers</p>

                <h2 className="text-value">0</h2>
              </div>
            </div>
          </Col>
          <Col xs="12" sm="6" lg="4">
            <div className="card-bg-info card-bg-3">
              <div className="card-bg-icon">
                <i class="fa fa-user-times" aria-hidden="true"></i>
              </div>
              <div className="card-bg-content">
                <p>Cancelled Subscriptions</p>

                <h2 className="text-value">0</h2>
              </div>
            </div>
          </Col>
         
          </Row>

        </div>

            
      </div>
    );
  }
}

export default Dashboard;
